body {
  letter-spacing: -.02em;
  color: #050505;
  background-color: #fbfbfb;
  font-family: Inter, sans-serif;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: 1.29;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

html {
  scroll-behavior: smooth;
}

p:last-child {
  margin-bottom: 0;
}

input, button, textarea, select {
  font-family: inherit;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: currentColor;
  text-decoration: none;
}

button {
  cursor: pointer;
  border: none;
  outline: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.is-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

video {
  max-width: 100%;
  height: auto;
  display: block;
}

.container {
  width: 100%;
  max-width: 375px;
  margin: 0 auto;
  padding: 0 20px;
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
    padding: 0 32px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    max-width: 1280px;
    padding: 0 100px;
  }
}















@font-face {
  font-family: Inter;
  src: local(Inter Medium), local(Inter Medium), url("Inter-Medium.684a61f9.woff2") format("woff2"), url("Inter-Medium.1be2400d.woff") format("woff"), url("Inter-Medium.3b890b5a.ttf") format("truetype");
  font-weight: 500;
  font-style: normal;
}

@font-face {
  font-family: Inter;
  src: local(Inter Regular), local(Inter-Regular), url("Inter-Regular.21fb113a.woff2") format("woff2"), url("Inter-Regular.aebce6b4.woff") format("woff"), url("Inter-Regular.573a4d5e.ttf") format("truetype");
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: Inter;
  src: local(Inter SemiBold), local(Inter-SemiBold), url("Inter-SemiBold.dde948a5.woff2") format("woff2"), url("Inter-SemiBold.45db5cc6.woff") format("woff"), url("Inter-SemiBold.dc799cc6.ttf") format("truetype");
  font-weight: 600;
  font-style: normal;
}

:root {
  font-synthesis: none;
  text-rendering: optimizelegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-text-size-adjust: 100%;
  font-family: sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
}

